import Vue from 'vue'
import Router from 'vue-router'
import monitor from '../components/monitor/monitor.vue'
import historyPhoto from '../components/historyPhoto/historyPhoto.vue'
Vue.use(Router)
export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'monitor',
      component: monitor
    },
    {
      path: '/historyPhoto',
      name: 'historyPhoto',
      component: historyPhoto
    }
  ]
})



