<template>
  <div class="historyPhoto">
    <el-container>
    <el-header>
      <el-date-picker type="date" placeholder="选择日期" v-model="theDate" style="width: 10%;" class="dateChoose" @change="dateChange()" value-format="yyyy-MM-dd"></el-date-picker>
      <span>历史违规信息</span>
    </el-header>
    <el-main>
      <div class="dateData" v-for="photoData in photosData" :key="photoData.date">
        <span class="date">{{photoData.date}}</span>
        <div class="datePhotos">
          <img v-for="photo in photoData.data" :key="photo" :src="'/static'+photo" alt="" style="width:400px;margin-left:20px;margin-bottom:20px;">
        </div>
      </div>
    </el-main>
    </el-container>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      date: '',
      photosData: [],
      theDate: ''
    }
  },
  created () {
    axios({
      url: '/img/all',
      type: 'get',
      data: {
      }
    })
    .then((res) => {
      console.log(res.data)
      this.photosData = res.data
    })
  },
  methods: {
    dateChange () {
      console.log(this.theDate)
      axios({
        url: '/img/oneDay/' + this.theDate,
        type: 'get',
        data: {
        }
      })
      .then((res) => {
        console.log(res.data)
        this.photosData = res.data
      })
    }
  }
}
</script>
<style>
  body {
    margin: 0;
    padding: 0;
    background:rgb(52, 63, 200)
  }
  .historyPhoto {
    background-color: rgba(132,141,255,0.5)
  }
  .el-header {
    background-color: rgba(132,141,255,0.5);
    color: #fff;
    text-align: center;
    line-height: 60px;
  }
  .el-main {
  }
  
  body > .el-container {
    margin-bottom: 40px;
  }
  
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
  
  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
  .dateChoose {
    float: left;
    margin-left: 50px;
  }
  .dateData {
    margin-top:20px;
  }
  .date {
    margin-left: 20px;
    color: #fff;
    font-size: 25px;
  }
  .datePhotos {
   margin-top: 20px;
  }
</style>



