<template>
  <div class="monitorBox">
    <div class="title">
        <i class="icon iconfont icon-shexiangtou"></i>
        <h1 class="title-font">安监易</h1>
        <el-button type="success" class="historyBtn" @click="goToHistory">历史违规</el-button>
      </div>
      <div class="content-wrapper">
        <div class="video-wrapper" style=" z-index: 10">
          <!-- <video id="example_video_1" class="video-js vjs-default-skin" controls preload="none" 
          data-setup="{}"
          width="100%"
          height="100%">
            <source src="rtmp://localhost:1935/rtmplive/home" type="rtmp/mp4">
          </video> -->
          <img src="http://localhost:8090/video1_480p.mjpg" alt="视频未加载成功,抱歉哦">
          <!-- <iframe src="http://10.23.12.235:8090/video1_480p.mjpg" frameborder="0" id="videoIframe"></iframe> -->
        </div>
        <!-- <div class="getPeople">
            <span>违规图片:</span>
        </div> -->
        <div class="warning-wrapper">
          <div ref="warnningPhotos" class="warnPeople">
            <photo :photos="warnningArray"></photo>
          </div>
        </div>
        <div class="photo-wrapper">
          <div ref="photosWrapper" class="getPhotoClass">
            <photo :photos="photoArray"></photo>   
          </div>
        </div>
      </div>
      <!-- <div class="warnning-wrapper" v-show="showWarnning.length" v-for="warn in showWarnning">
        <div  class="warnPhotos">
          <i class="icon iconfont icon-anquanmao"></i>
        </div>
      </div> -->
  </div>
</template>

<script type="text/acmascript-6">
  import photo from '../photo/photo.vue'
  import BScroll from 'better-scroll'
  import router from 'vue-router'
  import Vue from 'vue'

  const ERR_OK = 0;

  export default {


  data: function(){
    return {
      photoArray:[],
      a:1,
      b:1,
      warnningArray:[],
      isAlert:false,
      showWarnning:[]
    }
  },
  name: 'app',
  mounted () {
    this.setVideoView()
    // var aspctRatio = 16/9;
    // var myPlayer = videojs("example_video_1")
    // videojs.options.flash.swf = './../../../static/video-js.swf'


    // videojs("example_video_1").ready(function() {
    // var myPlayer = this;
    // // $(document).ready(function() {
    // //   var width = $('#'+myPlayer.id()).parent().width();
    // //   var height = $('#'+myPlayer.id()).parent().height();
    // //   // console.log(width)
    // //   // console.log(height)
    // //   myPlayer.width(width);
    // //   myPlayer.height(height);
    // //   // console.log(myPlayer.width)
    // //   // Vue.nextTick(function() {
    // //   //   window.onresize = function() {
    // //   //     alert("fdsf")
    // //   //     var width = $('#'+myPlayer.id()).parent().width();
    // //   //     myPlayer.width(width)
    // //   //   }
    // //   //   $(window).resize(function() {
    // //   //     alert("dfdfdfdf");
    // //   //   })

    // //   // })
    // // });

    // // myPlayer.play();
    
    // });



  },
  created() {
    // 开始连接
    // console.log('开始连接')
    // var socket = new window.SockJS('ws://192.168.130.1:8080/monitor/webSocket')
    // var stompClient = window.Stomp.over(socket)
    // stompClient.connect({}, function () {
    //   stompClient.subscribe('/warningAudio', function (message) {
    //     console.log(message)
    //     console.log(message)
    //     console.log(message)
    //   })
    // })
    // this.connectWebsocket()
    // setInterval(this.addPhotos,"2000")
    // setInterval(this.addWarnningPhoto,"3000")
    //
    var wsuri = ''
    var sock = null
    var index = 1
    var self = this
    if (window.location.protocol === "file:") {
        wsuri = "ws://127.0.0.1:9999"
    } else {
        wsuri = "ws://" + window.location.hostname + ":9999"
    }
    if ("WebSocket" in window) {
        sock = new WebSocket(wsuri)
    } else if ("MozWebSocket" in window) {
        sock = new MozWebSocket(wsuri)
    } else {
        console.log("Browser does not support WebSocket!");
    }
    if (sock) {
        sock.onopen = function() {
          console.log("Connected to " + wsuri)
        }
        sock.onclose = function(e) {
          console.log('websocket 关闭啦~')
          sock = null
        }
        sock.onmessage = function(e) {
          console.log(e.data)
          var newPhotos = {
            "src": e.data
          }
          if (index === 1) {
            console.log('1')
            self.warnningArray.unshift(newPhotos)
            self.$nextTick(() => {
              self.warnningPhotos = new BScroll(self.$refs.warnningPhotos,{click:true})
            })
            console.log(self.warnningArray)
            index = 2
          } else if (index === 2) {
            console.log('2')
            self.photoArray.unshift(newPhotos)
            self.$nextTick(() => {
              self.photoScroll = new BScroll(self.$refs.photosWrapper,{click:true})
            })
            console.log(self.photoArray)
            index = 1
          }

        }
    }
  },
  components: {
    photo
  },
  // sockets: {
  //   'chat message': function(msg) {
  //     this.message.push(msg)
  //   }
  // },
