    //     this.warnningPhotos = new BScroll(this.$refs.warnningPhotos,{click:true});
    //   },
            // 更新图片动画测试
      addPhotos: function() {
        var newPhotos = {
          "date":this.a++,
          "src":"http://static.galileo.xiaojukeji.com/static/tms/default_header.png"
        };
        // this.photoArray.splice(0,0,newPhotos);
        this.photoArray.unshift(newPhotos);
          // 刷新图片滚轮列表
          this.$nextTick(() => {
          this.photoScroll = new BScroll(this.$refs.photosWrapper,{click:true});
          })
      },

      //模拟警报图片
      addWarnningPhoto: function() {
        var newWPhotos = {
          "date":this.b++,
          "src":"http://static.galileo.xiaojukeji.com/static/tms/default_header.png",
          "photoType":1
        };

        this.warnningArray.unshift(newWPhotos)
          this.$nextTick(() => {
          this.warnningPhotos = new BScroll(this.$refs.warnningPhotos,{click:true});
          })
      },
      // 连接websocket,并获取数据
      connectWebsocket: function() {
        var webSocket = null;
            if('WebSocket' in window) {
                webSocket = new WebSocket('ws://10.21.23.171:8080/monitor/webSocket');
            }else{
                console.log('该浏览器不支持WebSocket');
            }

            webSocket.onopen = function () {
                console.log('建立连接');
            }

            webSocket.onclose = function () {
                console.log('关闭连接');
            }
        webSocket.onmessage = function (event) {
          console.log(event.data)
          var newPhotos = {
            "date":this.a++,
            "src": event.data
          };
          // this.photoArray.unshift(newPhotos);
          //   // 刷新图片滚轮列表
          //   this.$nextTick(() => {
          //   this.photoScroll = new BScroll(this.$refs.photosWrapper,{click:true});
          //   })
        }

        webSocket.onclose = function () {
            console.log('通信出错');
        }

        window.onbeforeunload = function(){
            webSocket.close();
        }
      },
      goToHistory: function () {
        this.$router.push({ name: 'historyPhoto'})
      },
      // 动态改变视频的宽和高,令其自适应
      setVideoView: function () {
        $(document).ready(function () {
          console.log($("#videoIframe").children())
          
        })
      }
      
  },
  watch: {

    //监听警报图片数组
    warnningArray: function(e) {
      e.forEach(element => {
        
        //警报显示
        // if(!element.hasShow) {
        //   this.showWarnning.unshift(element.photoType);
        //   element.hasShow = true;
        //   console.log($(window).height()/2)

        //   $(document).ready(function() {
        //     $('.warnPhotos').attr("top", "$(window).height()/2")
        //   })
        //   // this.$refs.warnPhotos.getAttribute('top') = document.body.clientHeight/2;
        // }
      });
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scope>
  body
    background:rgb(52, 63, 200)
    // background:black
    // background-image:url(./photos/3.png)
    margin: 0
    padding: 0

  .monitorBox
    position: fixed
    left:20px
    top:10px
    bottom:25px
    right:20px
    border: 1px solid white
    background-color: rgba(132,141,255,0.5)
    .title
      width:100%
      height:70px
      .icon
        display:inline-block
        font-size:30px
        color: #fff
        padding-left:20px
      .title-font
        display: inline-block
        line-height:30px
        font-size:30px
        color:#fff
        padding-left:10px
    .content-wrapper
      display:flex
      width:100%
      position:absolute
      top:73px
      bottom:0
      border-top:1px solid #949494
      .photo-wrapper
        height:100%
        flex: 0 0 15%
        width:15%
        overflow: hidden
      .video-wrapper
        flex:1
        width: 100%
        height: 100%
      .warning-wrapper
        height:100%
        width:15%
        overflow: hidden
        flex: 0 0 15% 
    .warnning-wrapper
      position: fixed
      z-index: 10
      width: 100%
      height: 100%
      overflow: auto
      background: rgba(7,27,27,0.8)
      .warnPhotos
        position: absolute
        .icon
          font-size: 200px
          color: red
          z-index:818889
  .getPeople {
    color: #ffffff;
    text-align:center;
  }
  .getPhotoClass {
    height: 100%;
    overflow: hidden;
  }
  .warnPeople {
    height: 100%;
    overflow: hidden;
  }
  .historyBtn {
    float: right;
    margin-top:15px;
    margin-right: 30px;
  }
  .video-js {
    width: 100%;
    height: 100%;
  }
  .video-wrapper img {
    width: 100%;
    height: 100%;    
  }
</style>



